body{
    height: 100%;
}
.get_started{
    display: flex;
    height: 100%;
}
.details *{
color: #fff;
}
.details{
    width: 400px;
    text-align: center;
    background-color: var(--skyblue);
    border-radius: 0px 10px 10px 0px;
    height: 100%;
}
.details h3{
padding-top: 70px ;
}
.details input{
    padding: 20px 0px;
    margin: 20px 0px;
    width: 80%;
    border: none;
    border-radius: 10px;
}
.scenario{
    
    width: 100%;
    text-align: center;
}
.scenario h3{
    padding: 80px 0px 50px 0px;
    color: var(--skyblue);
}
.scene{
    display: flex;
    padding-left: 5%;
    padding-right: 5%;
}
.scene div,.scene a{
    background: var(--skyblue);
    margin: 5px;
    border-radius: 10px;

}
.scene_img{
    padding: 20px;
}
.scene_img img{
    width: 80px;
}
.scene_img span{
    color: #fff;
}
.scene_text{
    width: 100%;
    color: #fff;
    text-decoration: none;
}
.scene_text:hover{
    color: black;
}