body{
    height: 100%;
}
.login {
    display: flex;
    justify-content: space-between;
    height: 100%;
}

.login-img {
    width: 40%;
    background: #9CBEE2;
    height: 100%;
}

.logo {
    position: absolute;
    left: 220px;
    top: 30%;

}

.img img{

    margin-top: 20%;
    width: 90%;
}
input{
padding-left: 10px;
width: 40%;
height: 5vh;
margin: 15px;
border: none;
border: 1px solid #BFBFBF;
background-color: white;
box-shadow: 10px 10px 5px #aaaaaa;
border-radius: 10px;
}
input:focus{
    border: none;

    border-left:10px solid var(--blue);
}
.login-Form {
    width: 80%;
}
.right{
    text-align: right;
    width: 70%;
    margin: 10px;
}

.bg-blue {
    background-color: var(--blue);
    color: #fff;
    border-radius: 100px;
    width: 120px;
    height: 40px;
    border: none;
}


.header {
    margin-top: 50px;
display: flex;
justify-content: space-between;
}
.header span{
    margin-right: 30px;
}
.text-area {
    text-align: center;
    margin-top: 150px;
}



.footer {
    position: absolute;
    bottom: 15px;
    left: 38%;
}