body{
    height: 100%;
}
.chatroom{
    display: flex;
    background-color: var(--white2);
    height: 100%;
}
.left{
    width: 80%;
    height: 100%;
}
.right{
    width: 20%;
    text-align: center;
    margin: 10px;
}
.img{
    background: #fff;
    border-radius: 10px;
    padding: 20px;
}
.img img{
    
width: 80px;
}
.right button{ 
    width: 100%;
    height: 7vh;
    margin-top: 10px;
    border: none;
    border-radius: 50px;
    font-weight: 800;
    font-size: medium;
    color: var(--blue);
}
.med{
    background: #fff;
    margin-top: 10px;
    border-radius: 10px;
}
.header{
    background: #fff;
    display: flex;
    width: 80%;
    border-radius: 0px 100px 100px 0px;
}
.logo{
    padding-right: 10px;
    border-right: 1px solid black;
    
}
.logo img{
    margin: 10px;
    margin-left: 20px;
    width: 150px;
    height: 80px;
}
.links{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-right: 50px;
    align-items: center;
}
.text{
    margin-left: 40px;
    }
.left button{
    width: 120px;
    height: 40px;
    margin-top: 10px;
    border: none;
    border-radius: 50px;
    font-size: 10px;
    color: #fff;
}
.bg-blue{
    background: var(--blue);

}
.bg-skyblue{
    background: var(--skyblue);
}